<template lang='pug'>
.supply-demand-page
	.section.hero
		navigation

		.header-text-container
			h1 Use this tool to help estimate the effect of supply & demand on the #[strong MCO] token over the next 6 months. #[strong This does not include any FOMO.]

	.section.grid.explaination
		.inner-content
			h2 Supply & Demand Estimation Tool #[em (beta)]

			.loading-indicator(v-if='!loaded')
				div.spinner-ruby
					div
					div
					div

			transition(name='fade')
				.slow-loader(v-if='loaded')

					p.statement.advice
						span.ruby DISCLAIMER:
						| ** This is not financial advice. I am a Software Engineer, not a financial advisor. The goal here is to show you how I came to my own prediction. Please... please do your own research. Use everything here as a starting point if you want, but take it as it is -- my prediction. Please send any feedback or bugs to #[a(href='mailto:mcolife@mikefogg.co') mcolife@mikefogg.co] **

					.grid-table-wrapper
						table.grid-table(:class='{ loaded: gridRows.values.marketcaps[0] }')
							thead
								tr
									th
									th(v-for='month in gridRows.months') {{ month }}
							tbody
								template(v-for='card in gridRows.cards')
									tr.important
										td(:colspan='gridRows.months.length + 1') {{ card.name }}
									tr
										td MCO Requirement
										td(v-for='val in card.mcoPer') {{ formatDecimal(val) }}
									tr
										td USD Requirement
										td(v-for='val in card.usdPer') {{ formatPrice(val) }}
									tr
										td New Cards
										td(v-for='val in card.newCards') {{ formatDecimal(val) }}
									tr
										td Total Cards
										td(v-for='val in card.totalCards') {{ formatDecimal(val) }}
								tr.empty: td(:colspan='gridRows.months.length + 1')
								tr.important
									td(:colspan='gridRows.months.length + 1').important Tokens
								tr
									td Total
									td(v-for='val in gridRows.tokens.exist') {{ formatDecimal(val) }}
								tr
									td Circulation
									td(v-for='val in gridRows.tokens.circulation') {{ formatDecimal(val) }}
								tr
									td New Locked Up
									td(v-for='val in gridRows.tokens.newLockup') {{ formatDecimal(val) }}
								tr
									td Total Locked Up
									td(v-for='val in gridRows.tokens.totalLockup') {{ formatDecimal(val) }}
								tr
									td Available
									td(v-for='val in gridRows.tokens.available') {{ formatDecimal(val) }}
								tr.empty: td(:colspan='gridRows.months.length + 1')
								tr.important
									td(:colspan='gridRows.months.length + 1') Cards
								tr
									td New Cards
									td(v-for='val in gridRows.cardCounts.new') {{ formatDecimal(val) }}
								tr
									td Total Cards
									td(v-for='val in gridRows.cardCounts.total') {{ formatDecimal(val) }}
								tr.empty: td(:colspan='gridRows.months.length + 1')
								tr.important
									td(:colspan='gridRows.months.length + 1') Value
								tr
									td Market Cap
									td(v-for='val in gridRows.values.marketcaps') {{ formatPrice(val) }}
								tr.price
									td MCO Value
									td(v-for='val in gridRows.values.prices') {{ formatPrice(val) }}

					.chart-container
						canvas#chart(width="800" height="300")

					h2 Custom Configuration

					p.note NOTE: Until I find a good way to do this programatically... #[strong most changes will require you to update the 'Monthly Dynamic MCO Reduction' value] to make sure the USD amount per card stays about the same after the dynamic reservations kick in. The default settings are my best predictions, but feel free to modify some of the values here if you disagree :) Also, feel free to ping me #[strong @mikef] if you want to be able to customize something else!

					.settings
						template(v-for='option in settingOptions')
							h3 {{ option.name }}
							.setting-group
								a(v-for='value in option.values' href='#' v-on:click.prevent='setOption(option.key, value.value)' :class='{ active: isOptionActive(option.key, value.value) }') {{ value.label }}

					p.statement.final I tried to make this extremely conservative so it includes no FOMO, no new features, none of the crazy crypto buzz when something starts to spike... etc... #[em And remember, this is just my prediction, feel free to make your own judgements :)]
</template>

<script>
// Partials
import Navigation from '@/components/elements/partials/navigation'
// Data
import Cards from '@/components/data/cards'
import DailyData from '@/components/data/daily'
// Helpers
import _ from 'lodash'
import numeral from 'numeral'
import Chart from 'chart.js'
import $ from 'jquery'

export default {
  name: 'SupplyDemandPage',
  data () {
    return {
			loaded: false,
      // Store the card chart
			cardChart: null,
      // Store the actual cards logic
			cardLogic: null,
      // Store the setting params (defaults)
			settingValues: {
				initialPrice: null,
				monthlyGrowthRate: 1.5,
				dynamicReductionAmount: 0.7,
				monthlyCirculationIncrease: 400000
			}
    }
  },
	computed: {
		apiResponse: function() {
			return this.$store.state.apiResponse
		},

		gridRows: function() {
			// console.log('testing')
      // Don't return anything until we're loaded
			const params = this.settingValues
			return this.cardLogic.rowsByMonths(7, params)
		},

		settingOptions: function() {
			// Don't return anything until we're loaded
			return [
				{
					name: 'Initial MCO Price',
					key: 'initialPrice',
					values: [
						{
							value: null,
							label: `Last 7 Day Average (${this.formatPrice(this.cardLogic.initialPrice)})`
						},
						{
							value: 10,
							label: '$10'
						},
						{
							value: 20,
							label: '$20'
						},
						{
							value: 30,
							label: '$30'
						},
						{
							value: 40,
							label: '$40'
						},
						{
							value: 50,
							label: '$50'
						},
						{
							value: 60,
							label: '$60'
						},
						{
							value: 70,
							label: '$70'
						},
						{
							value: 80,
							label: '$80'
						},
						{
							value: 90,
							label: '$90'
						},
						{
							value: 100,
							label: '$100'
						},
						{
							value: 187,
							label: '$187'
						}
					]
				}, {
					name: 'Card Reservation Growth Rate',
					key: 'monthlyGrowthRate',
					values: [
						{
							value: null,
							label: `Current (${this.formatPercent(this.cardLogic.currentGrowthRate)})`
						},
						{
							value: 1,
							label: '100%'
						},
						{
							value: 1.25,
							label: '125%'
						},
						{
							value: 1.5,
							label: '150%'
						},
						{
							value: 1.75,
							label: '175%'
						},
						{
							value: 2.0,
							label: '200%'
						},
					]
				}, {
					name: 'Monthly Dynamic MCO Reduction',
					key: 'dynamicReductionAmount',
					values: [
						{
							value: 1.0,
							label: 'Not Dynamic'
						},
						{
							value: 0.9,
							label: '10%'
						},
						{
							value: 0.8,
							label: '20%'
						},
						{
							value: 0.7,
							label: '30%'
						},
						{
							value: 0.6,
							label: '40%'
						},
						{
							value: 0.5,
							label: '50%'
						},
						{
							value: 0.4,
							label: '60%'
						},
						{
							value: 0.3,
							label: '70%'
						},
						{
							value: 0.2,
							label: '80%'
						},
						{
							value: 0.1,
							label: '90%'
						},
					]
				}, {
					name: 'MCO Added To Circulation Monthly',
					key: 'monthlyCirculationIncrease',
					values: [
						{
							value: 100000,
							label: '100k'
						},
						{
							value: 200000,
							label: '200k'
						},
						{
							value: 300000,
							label: '300k'
						},
						{
							value: 400000,
							label: '400k'
						},
						{
							value: 500000,
							label: '500k'
						},
						{
							value: 600000,
							label: '600k'
						},
						{
							value: 700000,
							label: '700k'
						},
						{
							value: 800000,
							label: '800k'
						},
						{
							value: 900000,
							label: '900k'
						},
						{
							value: 1000000,
							label: '1 Million'
						},
					]
				}
			]
		},
		chartData: function() {
			return {
				type: 'line',
				data: {
					labels: this.gridRows.months,
					datasets: [{
						label: 'Monthly Cards',
						yAxisID: 'y-axis-0',
						data: _.map(this.gridRows.cardCounts.total, (pr) => pr.toFixed(0)),
	          // Point
						pointBorderColor: 'rgba(37, 105, 149, 1.0)',
	          pointBackgroundColor: 'rgba(37, 105, 149, 1.0)',
	          pointHoverBackgroundColor: 'rgba(37, 105, 149, 1.0)',
	          pointHoverBorderColor: 'rgba(37, 105, 149, 1.0)',
	          pointBorderWidth: 6,
	          pointHoverRadius: 8,
	          pointHoverBorderWidth: 1,
	          pointRadius: 3,
	          // Background
						backgroundColor: [
							'rgba(37, 105, 149, 0.2)',
						],
	          // Border
						borderColor: [
							'rgba(37, 105, 149, 1.0)',
						],
						borderWidth: 2
					}, {
						label: 'MCO Value (USD)',
						yAxisID: 'y-axis-1',
						data: _.map(this.gridRows.values.prices, (pr) => pr.toFixed(2)),
	          // Point
						pointBorderColor: 'rgba(144, 15, 36, 0.15)',
	          pointBackgroundColor: 'rgba(144, 15, 36, 0.15)',
	          pointHoverBackgroundColor: 'rgba(144, 15, 36, 0.15)',
	          pointHoverBorderColor: 'rgba(144, 15, 36, 0.15)',
	          pointBorderWidth: 6,
	          pointHoverRadius: 8,
	          pointHoverBorderWidth: 1,
	          pointRadius: 3,
	          // Background
						backgroundColor: [
							'rgba(144, 15, 36, 0.05)',
						],
	          // Border
						borderColor: [
							'rgba(144, 15, 36, 0.15)',
						],
						borderWidth: 2
					}]
				},
				options: {
		      responsive: true,
		      maintainAspectRatio: false,
					legend: {
						position: 'bottom'
					},
					scales: {
						xAxes: [{
							ticks: {
								autoSkip: true
							}
						}],
						yAxes: [{
							id: 'y-axis-0',
							position: 'left',
							ticks: {
								beginAtZero:true
							},
							gridLines: {
	              color: 'rgba(37, 105, 149, 0.1)'
	            }
						}, {
							id: 'y-axis-1',
							position: 'right',
							ticks: {
								beginAtZero:true
							},
							gridLines: {
	              color: 'rgba(144, 15, 36, 0.1)'
	            }
						}]
					}
				}
			}
		}
	},
	components: {
		'navigation': Navigation
	},
  created() {
		// Set the card logic
		this.cardLogic = Cards
	},
	mounted() {
    // Load the data we need
		this.loadData()
	},
	watch: {
    settingValues: {
			handler: function(values) {

	      // Set the chart data, then fire update on it
				if (this.cardChart) {
          this.cardChart.data = this.chartData.data
          this.cardChart.update()
        }
			},
			deep: true
    },

		loaded: function(loaded) {
      // Set the chart up if we haven't yet!
			if (loaded && !this.cardChart) {
        // Wait until the next dom load
				this.$nextTick().then(() =>{
					// Build the chart
					var ctx = document.getElementById('chart')
					// Build the chart
					this.cardChart = new Chart(ctx, this.chartData)
			  })
			}
		}
  },
	methods: {
		// Go get the data from our api (or use the existing one)
		loadData: function() {
			if (this.apiResponse) {
        // We have it already! Use that
				this.handleApiResponse(this.apiResponse)
			} else {
				// We need it, so now let's store and use the json response
				$.get('https://mco-life-api.herokuapp.com/status').then(response => {
					this.$store.commit('apiResponse', response)
					this.handleApiResponse(this.apiResponse)
				}).catch(error => {
					console.log('error:', error)
				})
			}
		},

    // Actually handle the api response and set it
		handleApiResponse: function(response) {
			// Set the token amounts
			this.cardLogic.tokensExist = response.price.total_supply
			this.cardLogic.tokensInCirculation = response.price.available_supply

			// Store the days
			this.cardLogic.daily = response.daily

			// Set the initialprice as the daily avg for last 7 days
			this.cardLogic.initialPrice = _.mean(_.takeRight(_.map(this.cardLogic.daily, 'price_usd'), 7))
			this.cardLogic.currentGrowthRate = this.cardLogic.calculateGrowthRate()

			// Tell everything we're loaded
			this.loaded = true
		},
    // Set the option value when clicked
		setOption: function(optionName, value) {
			this.$set(this.settingValues, optionName, value)
		},
		isOptionActive: function(optionName, value) {
			return this.settingValues[optionName] == value
		},
		formatDecimal: (number) => {
			return numeral(number).format('0,0.00')
		},
		formatNumeral: (number) => {
			return numeral(number).format('0,0')
		},
		formatPrice: (number) => {
			return number !== Infinity ? numeral(number).format('$0,0.00') : ''
		},
		formatPriceSmall: (number) => {
			return numeral(number).format('$0,0')
		},
		formatPercent: (number) => {
			return numeral(number).format('0,0.00%')
		},
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
h2 {
	margin-top: 40px;

	em {
		color: $brand-ruby;
	}

	&:first-child {
		margin-top: 0;
	}
}

.section.grid {
	color: $brand-blue;
	text-align: left;
	font-size: 14px;

	table {
		width: 100%;
		opacity: 0;
		transition: opacity .6s;

		&.loaded {
			opacity: 1.0;
		}
	}

	tr.important {
		td {
			background: $brand-blue;
			color: #fff;
		}
	}

	tr.price {
		td {
			background: $brand-ruby;
			color: #fff;
			font-size: 18px;
		}
	}

	tr.empty {
		td {
			background: #fff;
			height: 20px;
			border: 0;
		}
	}

	td, th {
		padding: 5px 10px;
		border: 1px solid #e2e2e2;
	}

	th {
		text-align: center;
	}
}

.grid-table-wrapper {
  overflow-x: auto;
}

p.note {
	padding: 10px 20px;
	background: rgba($brand-ruby, 0.65);
	color: rgba(#fff, 0.8);
	border-radius: 3px;

	strong {
		color: #fff;
	}
}

.settings {
	margin-bottom: 40px;
}

.setting-group {
  margin-bottom: 16px;
	a {
		display: inline-block;
		padding: 10px 20px;
		margin: 0 10px 10px 0;
		font-size: 12px;
		border: 1px solid #d2d2d2;
		border-radius: 3px;
		text-decoration: none;

		&:hover {
			background: #f2f2f2;
			text-decoration: none !important;
		}

		&.active, &.active:hover {
			background: $brand-blue;
			color: #fff !important;
			border: 1px solid $brand-blue;
		}
	}
}

.section.explaination {
	text-align: left;
	color: $brand-blue;

	a {
		color: lighten($brand-blue, 30);

		&:hover {
			text-decoration: underline;
		}
	}
}

.statement {
	padding: 60px 0;

	&.advice {
		font-size: 20px;
		padding: 20px;
		background: rgba($brand-ruby, 0.1);
		border-radius: 6px;
	}

	span.ruby {
		color: $brand-ruby;
		margin-right: 10px;
	}
}

.answer {
	padding: 0;
	color: $brand-ruby;
	border-left: 4px solid $brand-ruby;
	padding: 20px;
	background: rgba($brand-blue, .10);
	border-radius: 6px;

	ul {
		list-style: none;
		margin: 0;
		padding: 0;
		color: $brand-blue;

		strong {
			color: $brand-ruby;
		}

		&.list {
			margin-left: 20px;
			list-style: square;

			li {
				margin: 20px 0;
			}
		}
	}

	font-size: 24px;
}

//
// Chart
//

.chart-container {
	position: relative;
	max-height: 300px;
	padding: 0px;
	margin-top: 40px;

	canvas#chart {
		height: 300px;
	}
}
</style>



