		answer: '<p>First, <a href="https://help.mona.co/our-company/monaco-as-a-visa-program-manager" target="_blank">Ill point you at their own FAQ</a>. Check it out!</p><p>Monaco is a VISA Program Manager. When the recent WaveCrest news came out, every company affected was forced to shut all cards off, effective immediately. Monaco is in no way affected since they do not work with WaveCrest. Period.</p><p>Incase youre not sure what really happened here, WaveCrest got in trouble with VISA for violating the terms of their agreement. This is what Monaco was trying to avoid, a middleman that controls their destiny. To do that, they took the long route to develop a relationship with VISA directly and are now able to issue cards without the need for a third party like WaveCrest.</p>',
