		tag: 'no-product',
		question: 'Monaco doesnt even have a product yet',
		answer: "<p>While I personally would consider the current app a product, I understand your concern about not having a working card out there right now or an app that functions like a wallet. While I cant combat that specific point it's very clear that the team over at Monaco is working tirelessly to get this app out the door. It's worth stating that they have an even larger stake in the game than we do. They obviously want to get out the door as fast as possible.</p><p>The recent WaveCrest news has also pointed out the importance of compliance and following the rules. Since Day 1, Monaco has made it very clear that compliance is at the core of what they are trying to accomplish and will not take any shortcuts to get out to market faster than regulations allow. While I can see that's annoying now in this fast-paced crypto world we live in every day, in the long run it will help build a company that's global, sustainable and prosperous.</p>",
